import React, { useState } from 'react';
import Nav from './Nav';
import './App.css';
import ItemPage from './ItemPage';
import CartPage from './CartPage';
import SubmitPage from './SubmitPage'
import {items} from './static-data';
// import ClientForm from '../ClientForm'

const App = () => {

  const [activeTab, setActiveTab] = useState(0);
  const [cart, updateCart] = useState([]);
  // const [client, updateClient] = useState([]);

  const handleTabChange = (index) => {
    setActiveTab(index);
  }
  const handleAddToCart = (item) => {
    updateCart([...cart, item.id])
  }
  const handleRemoveOne = (item) => {
    let index = cart.indexOf(item.id); updateCart([
        ...cart.slice(0, index),
        ...cart.slice(index + 1)
        ]
    );
}
  const renderCart = () => {
    let itemCounts = cart.reduce((itemCounts, itemId) => {
        itemCounts[itemId] = itemCounts[itemId] || 0;
        itemCounts[itemId]++;
        return itemCounts;
    }, {});

    let cartItems = Object.keys(itemCounts).map(itemId => {
        let item = items.find(item => item.id === parseInt(itemId, 10)
        );
        return { ...item, count: itemCounts[itemId]}
    });
    return (
      <CartPage items={cartItems} onAddOne={handleAddToCart} onRemoveOne={handleRemoveOne} />
      );
}
  const renderContent = () => {
    switch (activeTab) {
      case 0: return (
        <ItemPage items={items} onAddToCart={handleAddToCart} />
      );
      case 1: return renderCart();
      default: return null;
    }
  }

  return (
    <div className="App">
      <Nav activeTab={activeTab} onTabChange={handleTabChange} />
      <main className="App-content">
        {renderContent()}
      </main>
    </div>
  );
};

export default App;