import React from 'react';

const Nav = ({ activeTab, onTabChange }) => (
    <nav className="App-nav">
        
        <div id="main-header"><div class="container"><h1>Electricians Estimator</h1></div></div>
       
        <div id="main-header"></div>
            <div class="container"></div>
        <ul>
            <li className={`App-nav-item ${activeTab === 0 && 'selected'}`}><a href onClick={() => onTabChange(0)}>Items</a></li>
            <li className={`App-nav-item ${activeTab === 1 && 'selected'}`}><a href onClick={() => onTabChange(1)}>Quote</a></li>
        </ul>
    </nav>
);

export default Nav;


